import React from 'react';
import SplitPane from 'react-split-pane';
import './pane.css'
import TextIDE from './textIde'
import Layers from "./layers"
import Navigation from './navigation'
import PipeHelper from "../core/helpers/pipeHelper"
import { VM } from '../core/vm'
import { rootVM } from "../core/rootVm"

export default class Pane extends React.Component {
  constructor(props) {
    super(props);

    this.update()
  }

  componentDidUpdate(prevProps){
    if (prevProps !== this.props) this.update()
  }

  update(){
    rootVM.resetCache()

    this.pipeHelper = new PipeHelper(this.props.model.currentVM.pipe, this.props.model.currentVM, ()=>{
      this.props.model.rootVM.toValue()
    }, ()=>{
      let rootVM = this.props.model.currentVM
      while (rootVM.parent instanceof VM) { rootVM = rootVM.parent }
      rootVM.toValue()
    })
    let promises = rootVM.cache.valueSeq().toArray().filter((value) => {
      return value instanceof Promise
    })

    Promise.all(promises).then(()=> {
      this.pipeHelper.performPull()
        this.forceUpdate()
    })
  }

  render (){ return (
      <SplitPane split="vertical" minSize={50} maxSize={600} defaultSize={300} className="primary">
        <SplitPane split="horizontal" defaultSize={300}>
          <div>
            <h3><a href="https://lerd.gdn">LERD</a></h3>
            <Navigation model={this.props.model} controller={this.props.controller} pipeHelper={this.pipeHelper} />
            <button onClick={e => {
              let pipe = this.props.controller.createPipe()
              this.props.controller.setActiveVM(new VM(pipe))
            }}>Create Pipe</button>
          </div>
          <div>
            <button onClick={this.props.controller.createLayer}>Create Layer</button>
            <Layers model={this.props.model} controller={this.props.controller} pipeHelper={this.pipeHelper} />
          </div>
        </SplitPane>
        <div>
          <div>
            <input defaultValue={this.props.model.currentVM.pipe.desc} onBlur={ e=> {this.props.controller.changeCurrentPipeDesc(e.target.value)}} />
          </div>
          <TextIDE model={this.props.model} controller={this.props.controller} pipeHelper={this.pipeHelper} ref={(ref) => this.textIde = ref}/>
          <div className="hints">
            <h5>Keyboard shortcuts:</h5>
            <ul>
              <li>Cmd + s : Commit changes</li>
              <li>Ctrl + i : Insert pipe</li>
              <li>Ctrl + / : Change pipe</li>
              <li>Ctrl + c : Create pipe in pipe selector</li>
              <li>Ctrl + Shift + c : Create pipe with value</li>
            </ul>
          </div>

        </div>
      </SplitPane>
  )}
};



