import React from "react"
import Inspector from "react-inspector"
import PipeSuggest from "./pipe-suggest"
import {VM} from "../core/vm"
import { Set, List} from "immutable"

export default class DepControl extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeInput: false
    }
  }

  editForm(){
    return <PipeSuggest pipes={this.props.model.pipes}
                        autoFocus={true}
                        controller={this.props.controller}
                        model={this.props.model}
                        onFocusOut={() => {
                          this.setState({activeInput: false})
                          this.props.model.textide.setState({readOnly: false})
                        }}
                        onChange={(event, {suggestion, inlineCreated}) => {
                          event.preventDefault()
                          event.stopPropagation()
                          let origin = this.props.getOrigin()
                          this.props.pipeHelper.pipe.setDep(origin, this.props.requestedPipe, suggestion)
                          this.setState({activeInput: false})
                          this.props.model.textide.setState({readOnly: false})

                          if (inlineCreated) {
                            this.props.model.textide.performUpdate(this.props.model.textide.state.editorState)
                            let helper = this.props.pipeHelper
                            let id = this.props.model.currentVM.treeId()
                            origin = this.props.getOrigin()
                            let node1 = helper.tree.getIn(id.concat('origins').concat(List(origin)))
                            let node = node1.getIn(['deps'].concat(this.props.requestedPipe).concat(suggestion))
                            let nodePath = node.first().concat(suggestion)
                            let vm = helper.tree.getIn(nodePath.concat('vm'))
                            this.props.controller.setActiveVM(vm)
                          } else {
                            this.props.controller.reload()
                          }
                        }}

    />
  }

  render() {
    if (!Set.isSet(this.props.treeIds)) {
      debugger
    }
    return <div contentEditable={false} className="pipe-row dep-control">
      <div className="inspectors">
        {this.props.resolvedPipe && this.props.treeIds.map(treeId => {
          let nodePath = treeId.concat(this.props.resolvedPipe)
          let result = VM.toValue(this.props.pipeHelper.tree.getIn(nodePath.concat('result')))
          let vm = this.props.pipeHelper.tree.getIn(nodePath.concat('vm'))
          return <div className="inspector" key={nodePath}>
            <Inspector data={result} style={{display: 'inline-block'}}/>
            <u onClick={ event => {
              this.props.controller.setActiveVM(vm)
            }
            }>
              {(this.props.resolvedPipe||{}).desc}
            </u>
          </div>
        })}
      </div>
      <span>
        {
          !this.state.activeInput && (this.props.getOrigin()!== null) && (this.props.localResolve ?
            (
              <span>
                <u onClick={e => {this.props.pipeHelper.pipe.removeDep(this.props.localResolve, this.props.requestedPipe);this.props.controller.reload()}}>
                  x
                </u>
                &nbsp;
                <u onClick={e => {this.setState({activeInput: true});this.props.model.textide.setState({readOnly: true})}}>
                  { !this.state.activeInput && (this.props.resolvedPipe||{}).desc}
                </u>
                &nbsp;
                &#9657;
                &nbsp;
              </span>
            )
              :
            (
              <span className="create-action">
                <u onClick={e => {this.props.model.textide.setState({readOnly: true});this.setState({activeInput: true})}}>
                  create
                </u>
                &nbsp;
                &#9657;
                &nbsp;
              </span>
            )
          )
        }
        {
          this.state.activeInput && (
            <span>
              { this.editForm() }
              &nbsp;
              &#9657;
              &nbsp;
            </span>
          )
        }
        <strong>
        {this.props.requestedPipe.desc}
        </strong>
      </span>
    </div>
  }
}



